/*-->> header style <<--*/
.App-header {
  background-color: rgba(20, 90, 250, 1);
  padding: 3px 50px;
  color: white;
  position: fixed;
  z-index: 10;
  width: 100%;
}
.sk-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
/*-->> nav style <<--*/
.sk-header nav {
  width: 70%;
  position: absolute;
  right: 4px;
  z-index: 2;
  top: 6px;
}
nav ul {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
  margin: 0;
}
@media only screen and (max-width: 1023px) {
  .sk-header nav {
    width: 50%;
  }
  nav ul {
    right: -100%;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    -ms-flex-direction: column;
        flex-direction: column;
    position: relative;
    padding-top: 50px;
  }
  nav ul:before {
    content: '';
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
  }
  nav ul.mobile.open {
    display: -ms-flexbox;
    display: flex;
    right: 0;
  }
  nav ul.mobile.open:before {
    content: '';
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.8);
  }
}
  nav ul li {
    list-style: none;
    position: relative;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    padding-top: 4px;
    margin-left: 4px;
  }

    nav ul li a {
      text-transform: uppercase;
      font-size: 15px;
      color: #fff;
      font-weight: 500;
      text-decoration: none;
      -webkit-transition: all 300ms ease-in-out;
      -o-transition: all 300ms ease-in-out;
      transition: all 300ms ease-in-out;
      background-color: rgba(20, 90, 250, 1);
      display: -ms-flexbox;
      display: flex;
      -ms-flex-align: center;
          align-items: center;
      -ms-flex-pack: center;
          justify-content: center;
      padding: 6px 10px 4px;
      position: relative;
      z-index: 9;
      line-height: 1.4;

    }
    @media only screen and (max-width: 1023px) {
      nav ul li {
        width: 100%;
        right: -100%;
      }
      nav ul li:first-child {
        -webkit-transition: all 0.2s ease-in-out;
        -o-transition: all 0.2s ease-in-out;
        transition: all 0.2s ease-in-out;
      }
      nav ul li:nth-child(2) {
        -webkit-transition: all 0.4s ease-in-out;
        -o-transition: all 0.4s ease-in-out;
        transition: all 0.4s ease-in-out;
      }
      nav ul li:nth-child(3) {
        -webkit-transition: all 0.6s ease-in-out;
        -o-transition: all 0.6s ease-in-out;
        transition: all 0.6s ease-in-out;
      }
      nav ul li:nth-child(4) {
        -webkit-transition: all 0.8s ease-in-out;
        -o-transition: all 0.8s ease-in-out;
        transition: all 0.8s ease-in-out;
      }
      nav ul li:nth-child(5) {
        -webkit-transition: all 0.9s ease-in-out;
        -o-transition: all 0.9s ease-in-out;
        transition: all 0.9s ease-in-out;
      }
      nav ul.mobile.open li {
        right: 0;
      }
        nav ul li a {
          -ms-flex-pack: start;
              justify-content: flex-start;
        }
    }
    nav ul li a:hover {
      color: rgb(12, 25, 22);
    }

      nav ul li a span {
        height: 40px;
        width: 2px;
        background-color: rgba(20, 90, 250, 1);
        position: absolute;
        left: calc(50% - 2px);
        top: -40px;
        display: none;
      }
      nav ul li a span:after {
        content: '';
        height: 45px;
        width: 2px;
        background-color: rgba(20, 90, 250, 1);
        position: absolute;
        right: 8px;
        top: 0;
        -webkit-transform: rotate(25deg);
            -ms-transform: rotate(25deg);
                transform: rotate(25deg);
      }
      nav ul li a span:before {
        content: '';
        height: 45px;
        width: 2px;
        background-color: rgba(20, 90, 250, 1);
        position: absolute;
        left: 8px;
        top: 0;
        -webkit-transform: rotate(-25deg);
            -ms-transform: rotate(-25deg);
                transform: rotate(-25deg);
      }
/*-->> logo style <<--*/
.sk-header .logo {
  font-size: 24px;
  font-weight: bold;
  color: lightcoral;
  position: absolute;
  left: 5px;
  top: 10px;
}
.logo img {
  width: 115px;
  background: #fff;
}
@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



/* humburger menu */

#nav-icon1 {
  width: 30px;
  height: 24px;
  position: relative;
  -webkit-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
          transform: rotate(0deg);
  -webkit-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  cursor: pointer;
  position: absolute;
  top: 4px;
  right: 00px;
  padding: 6px;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  background: rgba(20, 90, 250, 1);
  display: none;
  border-radius: 3px;
  z-index: 10;
}
@media only screen and (max-width: 1023px) {
  #nav-icon1 {
    display: block;
  }
}
#nav-icon1 span {
  display: block;
  position: absolute;
  height: 4px;
  width: 30px;
  background: #fff;
  border-radius: 6px;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg) translate(6px, 6px);
      -ms-transform: rotate(0deg) translate(6px, 6px);
          transform: rotate(0deg) translate(6px, 6px);
  -webkit-transition: .25s ease-in-out;
  -o-transition: .25s ease-in-out;
  transition: .25s ease-in-out;
}

#nav-icon1 span:nth-child(1) {
  top: 0px;
}

#nav-icon1 span:nth-child(2) {
  top: 10px;
}

#nav-icon1 span:nth-child(3) {
  top: 20px;
}

#nav-icon1.open span:nth-child(1) {
  top: 12px;
  -webkit-transform: rotate(135deg) translate(-2px, -7px);
      -ms-transform: rotate(135deg) translate(-2px, -7px);
          transform: rotate(135deg) translate(-2px, -7px);
}

#nav-icon1.open span:nth-child(2) {
  opacity: 0;
  left: -30px;
}

#nav-icon1.open span:nth-child(3) {
  top: 12px;
  -webkit-transform: rotate(-135deg) translate(-7px, 2px);
      -ms-transform: rotate(-135deg) translate(-7px, 2px);
          transform: rotate(-135deg) translate(-7px, 2px);
}
	@media only screen and (max-width: 767px) and (min-width: 320px) {
    .sk-header nav {
      width: 70%;
    }
  }



